@import url(https://use.typekit.net/rqj4msc.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  font-size: 62.5%;
  font-family: 'Montserrat';
  font-weight: 200;
  font-style: normal;
  margin: 0;
  padding: 0;
  background: #decfdd;
  color: #252525;
  width: 100%;
  height: 100%;
}

h1,
h2,
h3,
.plantInfo {
  font-family: moret, serif;
  font-weight: 300;
  font-style: normal;
  color: #4f7b71;
}

h1 {
  text-align: center;
  letter-spacing: 0.05rem;
  font-size: 3.8rem;
  margin: 0;
  padding: 0;
}

h2 {
  font-size: 1.7rem;
  margin-bottom: 0.5rem;
}

p {
  font-size: 1.15rem;
  letter-spacing: 0.02rem;
  margin-bottom: 2.2rem;
}

span {
  color: #000000;
}

.plantLogo,
.plantTaskLogo {
  width: 5.5%;
  padding-right: 0.5rem;
  vertical-align: bottom;
}

.formContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 1.5rem;
}

.wrapper,
.taskContainer {
  width: 48%;
}

.taskContainer,
.formPlantContainer,
.plantTaskContainer {
  padding: 3rem;
  margin-bottom: 3rem;
  background-color: #f2e7f1;
  width: 48%;
}

form {
  padding-bottom: 1.5rem;
  width: 100%;
}

input {
  width: 50%;
  padding: 0.4rem;
}

button,
input[type=text],
button[type=submit] {
  text-align: center;
  font-family: 'Montserrat';
  letter-spacing: 0.08rem;
  font-size: 1rem;
}

button,
button[type=submit] {
  font-family: moret, serif;
  font-weight: 800;
  background-color: #252525;
  color: #ffffff;
  padding: 0.4rem;
  margin-left: 0.4rem;
  padding-left: 1rem;
  padding-right: 1rem;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  margin-top: 1rem;
}

button:hover,
button[type=submit]:hover {
  background-color: #4f7b71;
  cursor: pointer;
}

ul,
li {
  padding: 0.2rem;
  font-size: 1.2rem;
  list-style-type: none;
}

ul {
  margin-bottom: 0;
}

.info,
.task {
  padding: 0.5rem 0;
  margin-right: 1.5rem;
}

.title {
  color: #4f7b71;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(65%, rgba(255,255,255,0)), color-stop(65%, #ffffff));
  background: linear-gradient(180deg, rgba(255,255,255,0) 65%, #ffffff 65%);
}

.plantInfo {
  color: #000000;
  font-size: 1.45rem;
  letter-spacing: 0.1rem;
}

@media (max-width: 1280px) {
  .plantLogo,
  .plantTaskLogo {
    width: 7%;
  }
}

@media (max-width: 980px) {
  .plantLogo,
  .plantTaskLogo {
    width: 7%;
  }

  input {
    width: 65%;
    padding: 0.4rem;
  }
}

@media (max-width: 950px) {
  .plantLogo,
  .plantTaskLogo {
    width: 10%;
  }
}

@media (max-width: 750px) {
  .plantLogo,
  .plantTaskLogo {
    width: 10%;
  }
}

@media (max-width: 650px) {
  .plantLogo,
  .plantTaskLogo {
    width: 10%;
  }
}

@media (max-width: 410px) {
  
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

